.chapter-one {
    position: relative;
    padding-bottom: 360px;
    background: url(../img/bg4.jpg) top center no-repeat;

    .container {
        display: grid;
        grid-template-columns: 365px 1fr;
        gap: 100px 195px;
        padding-top: 178px;
    }

    .ocop-logo {
        display: block;
        font-size: 0;
        width: 365px;
        height: 175px;
        background: url(../img/ocop-2.png) center center/365px no-repeat;
    }

    .list {
        border-radius: 5px;
        background: rgba(255, 255, 255, 0.20);
        padding: 20px;
        display: grid;
        gap: 25px;

        a {
            color: var(--gray-white, #FFF);
            text-shadow: 2px 2px 0px rgba(0, 0, 0, 0.10);
            font-family: "PoppinsZ";
            font-size: 18px;
            font-weight: $font-weight-medium;
            text-transform: uppercase;
            padding-left: 30px;
            position: relative;

            &::before {
                position: absolute;
                content: "";
                width: 4px;
                height: 4px;
                background: #fff;
                top: 8px;
                border-radius: 50%;
                left: 0;
            }
        }
    }

    .text {
        width: 575px;
        padding: 40px;
        border-radius: 20px;
        background: #FFF;
        display: grid;
        gap: 20px;

        .title {
            color: var(--gray-black, #000);
            font-family: "PoppinsZ";
            font-size: 24px;
            font-weight: $font-weight-semibold;
            line-height: 28px;
        }

        >* {
            color: var(--gray-black, #000);
            font-family: "PoppinsZ";
            font-size: 16px;
            line-height: 28px;
        }
    }

    .more-news {
        grid-column: 1/3;
    }
}